img {
  display: block;
  object-fit: cover;
}

._logo {
  height: 30px;
}

._iconlink {
  height: 24px;
}

.input-group ._input-minw {
  min-width: 278px;
}

@media (max-width: 576px) {
  .input-group ._input-minw {
    min-width: inherit;
